<template>
    <section class="catalog-wrapper" v-if="error.length === 0">
        <h1 class="title">{{categoryId ? (categoryId + ' ' + subCategoryId) : searchRequest}}</h1>

        <div v-if="products.length !== 0" class="search-result-wrapper" id="grid"
             v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">

            <Card v-for="item in products" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="buttonText"
                  :basket-data="basketData"
                  v-on:buy="onBuy">
            </Card>

        </div>

        <div class="spinner" v-observe-visibility="onBottomReached">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </div>
    </section>

    <section class="layout" v-else>
        <h1 class="title">{{error}}</h1>
    </section>
</template>

<script>
    import Card from "../components/catalog/Card";
    import LoadingSpinner from "../components/LoadingSpinner";

    import {CatalogAPI} from "../../api/catalog";
    import {INTERNAL_SERVER_ERR} from "../../common/errors";

    import {productUpdater} from "../../api/WSProductsUpdater";

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'
    Vue.use(VueObserveVisibility);

    import myStorage from "../../common/cache";
    const cache = myStorage('api');

    const itemsPerPage = 9;
    const PRODUCTS_UPDATE_TIME = 10;

    export default {
        name: "CatalogComponent",

        components: {Card, LoadingSpinner},

        props: {
            categoryId: {
                type: String,
            },

            subCategoryId: {
                type: String,
            },

            searchRequest: {
                type: String,
            },

            buttonText: {
                type: String,
                default: "Заказать",
            },

            basketData: {
                type: Array,
                required: true
            },
        },

        data: () => {
            return {
                isLoading: true,
                hasMoreToLoad: false,

                error: "",

                lastPageNumber: 1,
                paginatedProducts: [],

                productUpdater: null,
                productUpdaterInitTimer: null,
            }
        },

        methods: {
            async init() {
                this.closeSocket();

                this.lastPageNumber = 1;
                this.paginatedProducts = [];
                this.hasMoreToLoad = false;
                this.isLoading = true;

                if (this.searchRequest) {
                    await this.search(this.searchRequest);
                } else if (this.categoryId) {
                    await this.getProducts();
                }

                this.isLoading = false;
            },

            onBuy(item) {
                this.$emit('buy', item);
            },

            onBottomReached(isVisible) {
                if (!this.isLoading && this.hasMoreToLoad && this.error.length === 0 && isVisible) {
                    this.isLoading = true;
                    this.getMoreProducts()
                        .then(() => {
                            this.isLoading = false;
                        })
                }
            },

            async getProducts() {
                let payload = {
                    categoryId: this.categoryId,
                    subCategoryId: this.subCategoryId,
                    itemsPerPage
                };

                try {
                    let result = await CatalogAPI.getProductsByCategory(payload);
                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            pageNumber: 1,
                            data: json.data,
                        });
                        this.paginatedProducts.sort((a, b) => {
                            if (a.pageNumber && b.pageNumber) {
                                return a.pageNumber - b.pageNumber
                            } else {
                                return 0
                            }
                        });

                        this.hasMoreToLoad = json.hasMoreToLoad;

                        this.runProductUpdater();
                    } else {
                        console.error({result}, "while loading products");
                        this.error = INTERNAL_SERVER_ERR;
                    }
                } catch (e) {
                    console.error({e}, "while loading products");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async getMoreProducts() {
                this.lastPageNumber++;
                this.hasMoreToLoad = false;
                let payload = {
                    categoryId: this.categoryId,
                    subCategoryId: this.subCategoryId,
                    itemsPerPage,
                    pageNumber: this.lastPageNumber,
                };

                try {
                    let result = await CatalogAPI.getMoreProductsByCategory(payload);

                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            pageNumber: payload.pageNumber,
                            data: json.data,
                        });
                        this.paginatedProducts.sort((a, b) => {
                            if (a.pageNumber && b.pageNumber) {
                                return a.pageNumber - b.pageNumber
                            } else {
                                return 0
                            }
                        });

                        this.hasMoreToLoad = json.hasMoreToLoad;
                    } else {
                        console.error({result}, "while loading more products");
                        this.error = INTERNAL_SERVER_ERR;
                    }

                } catch (e) {
                    console.error({e}, "while loading more products");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async search() {
                try {
                    const result = await CatalogAPI.search(this.searchRequest);
                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            data: json.data,
                        });

                        this.runProductUpdater();
                    } else {
                        console.error({result}, "while search");
                        this.error = INTERNAL_SERVER_ERR;
                    }
                } catch (e) {
                    console.error({e}, "while search");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async runProductUpdater() {
                console.log("product updater");

                let downloadAndUpdateData = async () => {
                    let res = await this.productUpdater.sendGetData();

                    for (let itemId in res) {

                        for (let i = 0; i < this.paginatedProducts.length; i++) {

                            for (let prId in this.paginatedProducts[i].data) {

                                if (prId === itemId) {
                                    this.paginatedProducts[i].data[prId].isCountInStockTooBig = res[itemId].isCountInStockTooBig;
                                    this.paginatedProducts[i].data[prId].countInStock = res[itemId].countInStock;
                                    break
                                }
                            }
                        }
                    }

                    await this.synchronizeWithCache();
                    this.productUpdaterInitTimer = setTimeout(() => this.runProductUpdater(), PRODUCTS_UPDATE_TIME * 1000);
                };

                this.productUpdaterInitTimer = null;
                try {
                    if (!this.productUpdater || this.productUpdater.isClosed) {
                        let payload;
                        if (this.categoryId) {
                            payload = {categoryId: this.categoryId, subCategoryId: this.subCategoryId};
                        } else if (this.searchRequest) {
                            let childrenIds = [];
                            this.products.forEach((item) => {
                                childrenIds = [...childrenIds, ...Object.keys(item.childrenData)];
                            });

                            payload = {additional: childrenIds};
                        }

                        this.productUpdater = productUpdater;
                        await this.productUpdater.start();
                        await this.productUpdater.sendHandshake(payload);
                        await downloadAndUpdateData();

                    } else if (this.productUpdater && this.productUpdater.isWorking()) {
                        await downloadAndUpdateData();
                    }
                } catch (e) {
                    console.error(e);

                    if (this.productUpdater) {
                        this.productUpdater.close();
                        this.productUpdater = null;
                    }
                }
            },

            async synchronizeWithCache() {
                if (this.searchRequest) {
                    let cacheId = `/catalog/search/request:${this.searchRequest};`;
                    let response = await cache.get(cacheId);
                    if (response) {
                        await cache.updateJSON(cacheId, this.products);
                    }
                } else if (this.categoryId) {

                    for (let i = 0; i < this.paginatedProducts.length; i++) {
                        let item = this.paginatedProducts[i];

                        let cacheId;
                        if (item.pageNumber === 1) {
                            if (this.subCategoryId) {
                                cacheId = `/catalog/getProductsByCategory/categoryId:${this.categoryId};subCategoryId:${this.subCategoryId};`
                            } else {
                                cacheId = `/catalog/getProductsByCategory/categoryId:${this.categoryId};`
                            }
                        } else {
                            if (this.subCategoryId) {
                                cacheId = `/catalog/getMoreProductsByCategory/categoryId:${this.categoryId};subCategoryId:${this.subCategoryId};pageNumber:${item.pageNumber};`
                            } else {
                                cacheId = `/catalog/getMoreProductsByCategory/categoryId:${this.categoryId};pageNumber:${item.pageNumber};`
                            }
                        }

                        let response = await cache.get(cacheId);
                        if (response) {
                            await cache.updateJSON(cacheId, item.data);
                        }
                    }
                }
            },

            closeSocket() {

                if (this.productUpdater) {
                    this.productUpdater.close();
                }

                if (this.productUpdaterInitTimer) {
                    clearTimeout(this.productUpdaterInitTimer);

                }

                this.productUpdater = null;
                this.productUpdaterInitTimer =  null;
            },
        },

        computed: {
            products: function() {
                let res = [];
                this.paginatedProducts.forEach(item => {
                    if (item.data) {
                        res.push(...item.data);
                    }
                });
                return res;
            },

            searchResultWrapperMarginBottom: function() {
                /*if (this.isBaskeWindowVisible) {
                    return 230 + "px"
                } else {
                    return 0 + "px"
                }*/

                return 230 + "px"
            },

        },

        mounted() {
            this.init();
        },

        beforeDestroy() {
            this.closeSocket();
        },
    }
</script>

<style scoped lang="sass">
    @import '../../assets/basic'

    .catalog-wrapper
        font-family: Roboto, sans-serif

        .title
            margin-top: 1rem
            text-align: center
            font-size: 1.8rem
            font-weight: normal
            font-family: Oswald, sans-serif

        .spinner
            display: flex
            justify-content: center
            margin-top: 2rem


        .search-result-wrapper
            display: grid
            grid-gap: 15px
            grid-template-columns: repeat( auto-fill, minmax(250px, 1fr) )

    /*@include mobile
        grid-template-columns: 1fr

    /*@include tablet
        grid-template-columns: repeat(2, 1fr)

    /*@include laptop
        grid-template-columns: repeat(3, 1fr)

    /*@include laptop-l
        grid-template-columns: repeat(4, 1fr)

</style>